@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    padding: v.$separacion 0;

    @include m.telefono{
        display: flex;
        justify-content: space-between;
    }

    button{
        font-size: 2rem;
        max-width: 100%;
        @include m.telefono{
            max-width: 50%;
        }
        margin: 0 0 1rem 0;

    }
}

.ocultar{
    visibility: hidden;
}
